p {
  font-family: 'B612', sans-serif;
}
.line {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
.line > div {
  width: 40%;
  height: 1px;
  background-color: white;
}
.line p {
  color: white;
}
@media only screen and (max-width: 533px) {
  .line {
    height: 25px;
    margin-top: 0px;
  }
}
